.data-bar{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
  background-color: #f0f3f6;
  margin-top: 80px;
  .month{
    padding: 1em;
  }
  .days{
    display: flex;
    justify-content: center;
    align-items: center;
    .selected{
      background-color: #04a1a4;
    height: 70px;
    margin-top: 10px;
    border-radius: 0 0 10px 10px;
    box-shadow: 0px 4px 5px -2px;
    color: white;
    }
    div{
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: space-around;
      box-sizing: border-box; 
      width: 50px;
      height: 60px;
      padding: 5px;
      
    }
  }
}